import React from "react";
function shuffle(a) {
  for (let i = a.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [a[i], a[j]] = [a[j], a[i]];
  }
  return a;
}
export default function Score(props) {
  const { status, data, onSubmit } = props;
  const btnType = status ? "secondary" : "primary";
  const btnText = status
    ? "Get recommendation in progress with Brain Pet insurance API ..."
    : "Investigate with Brain Pet insurance API";
  const { warnings={} , result={}, explain =[] } = data || {};
  const { columns_warnings } = warnings||{} ;
  console.log({data})
  const suspiciousScore = result.scores && (+result.scores[1].score).toFixed(4);
  const suspicioussness = result && result.prediction === "no";
  const suspisiousLabel = suspicioussness
    ? "Will be interested by a pet insurance "
    : "Won't be interested by a pet insurance";
  const badgeClassName =
    "badge  badge-" + (!suspicioussness ? "danger" : "success");
  return (
    <div className="align-self-center">
      {(!data || (data && (!data.count || data.count)) === 0) && (
        <div className="card">
          <div className="card-body">
            <button
              type="submit"
              onSubmit={onSubmit}
              className={`btn btn-${btnType}`}
            >
              {btnText}
            </button>
          </div>
        </div>
      )}
      {data && data.count > 0 && (
        <div className="card">
          <div className="card-body">
            <h5 className="card-title">
              {" "}
              {suspisiousLabel}{" "}
              <span className={badgeClassName}>{suspiciousScore}</span>
            </h5>
            <ul className="list-group">
              {explain //shuffle(Object.keys(props.dataToScore))
                //.slice(0, 5)
                .map((key, idx) => {
                  return (
                    <li
                      className="list-group-item list-group-item-info"
                      key={"info-" + key.column} //props.dataToScore[key]}
                    >
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "space-between"
                        }}
                      >
                        <div>
                          <span>
                            {" "}
                            {"Because "}{" "}
                            <span className="badge badge-info">
                              {key.column}
                            </span>{" "}
                            {" is "}{" "}
                          </span>
                          <span className="badge badge-info">
                            {props.dataToScore[key.column]}
                          </span>
                        </div>
                        <div className="progress" style={{ width: "30%" }}>
                          <div
                            className="progress-bar"
                            role="progressbar"
                            style={{
                              width:
                                Math.floor(100 * Math.abs(key.importance)) + "%"
                            }}
                            aria-valuenow="100"
                            aria-valuemin="0"
                            aria-valuemax="100"
                          />
                        </div>
                      </div>
                    </li>
                  );
                })}
              {false &&
                Array.isArray(columns_warnings) &&
                columns_warnings.map(warn => {
                  return (
                    <li
                      className="list-group-item list-group-item-warning"
                      key={"warn-" + warn.column}
                    >
                      ‚ö†Ô∏è{" "}
                      <span className="badge badge-warning">{warn.column}</span>
                      {`¬†` + warn.messages.map(m => m.text).join(",")}
                    </li>
                  );
                })}
            </ul>
            <hr />
            <button
              type="button"
              onClick={() =>
                alert("Your customer was added to the next campaign")
              }
              className={`btn btn-success`}
            >
              Add to campaign
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
Score.defaultProps = {
  data: { columns_warnings: [] }
};

